<template>
  <div>
    <vSearch @getOption="getOption" @searchInfo="searchInfo" @clearSearchInfo="clearSearchInfo" :pageType="pageType"></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort = "{prop: 'customerName', order: 'descending'}"
      >
      <el-table-column
        prop="cname"
        label="客户名称"
        sortable>
      </el-table-column>
      <el-table-column
        prop="id"
        label="客户号"
        sortable>
      </el-table-column>
      <el-table-column
        label="被买断仓单">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'warehouse')"
            type="text"
            size="small">
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="loanAmmount"
        label="总放款金额（人民币）"
        sortable>
      </el-table-column>
      <el-table-column
        prop="basicValue"
        label="原仓单总价值（人民币）"
        sortable>
      </el-table-column>
      <el-table-column
        prop="currentValue"
        label="现仓单总价值（人民币）"
        sortable>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage">
      </el-pagination>
    </div>
    <el-dialog title="被买断仓单" :visible.sync="dialogFormVisible">
      <span>仓单编号：</span>
      <el-input type="text" v-model="inputText" style="width:180px"/>
      <el-button type="primary">搜索</el-button>
      <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort = "{prop: 'customerName', order: 'descending'}"
      >
        <el-table-column
          prop="loanAmmount"
          label="仓单编号"
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          prop="basicValue"
          label="仓单现价/原价（人民币）"
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          prop="currentValue"
          label="合同编号"
          sortable
          width="180">
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from '../components/Search.vue';
import * as ajaxUrl from '../ajax.config.js';// 引入接口地址

export default {
  data() {
    return {
      pageType: 3,
      searchForm: {
        option: '',
        inputText: '',
      },
      inputText: '',
      currentPage: 1,
      pageSize: 0,
      totalPage: 0,

      dialogFormVisible: false,

      tableData: [],
    }
  },
  mounted(){
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText){
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(){
      this.axios.get(ajaxUrl.CUSTOMERINFO, {
        params: {
          pageSize: this.pageSize,
          pageNum: this.currentPage,

        }
      }).then((res) => {
        this.tableData = res.data.resData;
        this.totalPage = res.data.page.total;
        this.pageSize = res.data.page.pageSize;
      }).catch((err) => {
        console.log(err)
      })
    },
    clearSearchInfo(){
      this.searchForm.option = '';
      this.searchForm.inputText = '';
    },
    openDialog(row, type){
      this.dialogFormVisible = true;
      console.log(row)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
  components: {
    vSearch
  }
}
</script>

<style>
.pagination {
  text-align: center;
}
</style>



